.call-room {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
  }
  
  .video-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80%;
    position: relative;
  }
  
  .local-video {
    width: 200px;
    height: 150px;
    border: 2px solid #ccc;
    margin: 10px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .remote-video {
    width: 200px;
    height: 150px;
    border: 2px solid #ccc;
    margin: 10px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .end-call-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #ff4d4d;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .end-call-button:hover {
    background-color: #ff1a1a;
  }
  